.galeria-container {
  padding: 20px;
  position: center;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 1fr));
  gap: 20px;
}

.imagen {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.imagen img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.imagen::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  transition: opacity 0.3s ease;
  opacity: 0; /* Oculta el fondo blanco */
}

.imagen:hover::before {
  opacity: 0.8; /* Muestra el fondo blanco */
}

.imagen:hover img {
  transform: scale(1.05); /* Aumenta ligeramente el tamaño al hacer hover */
}

h1 {
  color: #f55207;
  text-align: center;
  font-weight: bold;
}

/* Media query para dispositivos móviles */
@media (max-width: 768px) {
  .galeria {
    grid-template-columns: repeat(1, 1fr); /* Cambia a una columna */
  }
}

