/* WhatsAppButton.css */

.whatsapp-button {
    position: fixed;
    bottom: 100px;
    right: 20px;
    width: 60px;
    height: 60px;
    background-color: #04ff60;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    z-index: 1000; /* Asegura que esté por encima del contenido */
  }
  
  .whatsapp-button img {
    width: 70%;
  }
  
  .whatsapp-button:hover {
    background-color: #128c7e;
  }
  