.contacto {
    padding: 20px;
    position: center;
  }
  
  .contacto h2 {
    margin-bottom: 20px;
  
  }
  
  .contacto__info {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    text-align: center;
    font-size: 15px;
  }
  
  .info__item {
    
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    border-width: 1px;
    border-style: solid;
    border-color: #f55207;
  
  }
  
 