.footer-container {
    background-color: #000000; /* Fondo oscuro */
    color: #f55207; /* Texto en color naranja */
    padding: 20px;
    text-align: center;
    height: 50px;
  }
  
  .footer-container p {
    margin: 0;
    font-size: 14px;
    font-weight: bold;
  }
  
  .footer-container a {
    color: #f55207; /* Color naranja para enlaces */
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer-container a:hover {
    color: #ff7e2b; /* Cambio de color al hacer hover */
  }
  