.navbar {
  padding-bottom: none;
  background-image: linear-gradient(to top, #f55207 0%, #f9d423 100%);
  background-blend-mode: multiply;
  padding: 10px 20px;
  position: center;
  top: 0;
  left: 0;
  width: auto;
  z-index: 1000;
}

.container {
  display: flex;
  justify-content:space-between;
  align-items: center;
  padding: 0;
  height: 50px; /* Ajusta la altura del navbar */
}

.navbar__brand {
  display: flex;
  align-items: center;
}

.navbar__brand img {
  height: 30px; /* Ajusta la altura de la imagen en relación con el navbar */
  margin-right: 10px;
}

.navbar__brand h1 {
  color: #000000;
  font-size: 1.5rem;
}

.navbar__menu {
  display: flex;
  justify-content: flex-end;
}

.navbar__menu ul {
  list-style: none;
  padding: 0;
  display: flex;
}

.navbar__menu ul li {
  margin-right: 50px;
}

.navbar__menu ul li:last-child {
  margin-right: 0;
}

.navbar__menu ul li a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem; /* Ajusta el tamaño del texto del menú */
}

.navbar__toggle {
  display: none;
}

@media (max-width: 768px) {
  .container {
    padding: 0;
  }

  .navbar__brand {
    flex-grow: 1;
    text-align: center;
    margin-right: auto; /* Alinea el logo a la izquierda */
    margin-left: 10px; /* Añade un margen al menú de hamburguesa */
  }

  .navbar__brand img {
    margin-right: 5px;
  }

  .navbar__brand h1 {
    font-size: 1rem;
  }

  .navbar__menu {
    display: none;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50px; /* Ajusta el top en relación al navbar */
    right: 0; /* Ajusta el menú a la derecha */
    width: 100%; /* Ocupa todo el ancho */
    background-color: #f9d423;
    padding: 10px 0;
  }

  .navbar__menu.active {
    display: flex;
  }

  .navbar__toggle {
    display: block;
    cursor: pointer;
    margin-right: 10px; /* Añade un margen a la derecha */
  }

  .navbar__toggle .bar {
    width: 25px;
    height: 3px;
    background-color: #ffffff;
    margin-bottom: 5px;
  }

  .navbar__menu ul {
    flex-direction: column;
    padding: 0;
  }

  .navbar__menu ul li {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.navbar__menu ul li a {
  color: #000000;
  font-weight: bold;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease; /* Transición para el color del texto */
}

.navbar__menu ul li a:hover {
  color: #ffffff; /* Cambia el color al pasar el mouse */
}
