/* Estilos para el slider */
.slider-container {
  padding-top: 5.5%;
  padding-bottom: 0%;
  margin-top: 0px; /* Altura del navbar */
  width: 100%;
  max-width: 1000px; /* Máximo ancho del slider */
  margin: auto; /* Centra el slider */
  position: center;
}

.slider-container .slick-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 0%;
}

.slider-container .slick-slide img {
  width: 73vw; /* Ocupa todo el ancho del viewport */
  height: 550px; /* Altura fija */
  object-fit: cover; /* Ajusta la imagen dentro del contenedor */
}


