.servicios {
  padding: 20px;
  position: center;

}

.servicios h2 {
  margin-bottom: 20px;
}

.servicios__container {
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 1fr));
  gap: 70px;
  
}

.servicios__card {
  padding: 10px;
  /*background-image: linear-gradient(-225deg, #E3FDF5 0%, #fcc2f0 100%);*/
  /*background-image: linear-gradient(-225deg, #FFFEFF 0%, #D7FFFE 100%);*/
  /*background-color: aliceblue;*/
  /*background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);*/
  
  background-blend-mode: screen;
  display: flex;
  flex-direction: column;
  /*justify-content: space-between;*/
  justify-content:center;
  align-items: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  border-radius: 20px;
  height: 400px; /* Altura fija para todas las cards */
 
 
  font-size: 16px;
  font-weight: 400;

text-align:justify;
  border-width: 1px;
  border-style:solid;
  border-color: #f55207;
}

.servicios__card img {
  width: 100%;
  height: 200px; /* Tamaño fijo para todas las imágenes */
  object-fit: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.servicios__card h3 {
  margin-top: 10px;
  font-size: 1.3rem; /* Tamaño del título */
  color: #000000;
  font-weight:bold;
}

.description {
  overflow: hidden;
  max-height: 80px; /* Altura máxima de la descripción recortada */
 text-align: justify;
}

.full-description {
  overflow: hidden;
  max-height: none; /* Desplegar la descripción completa */
}

.servicios__card p {
  margin-bottom: 10px;
}

.servicios__card button {
  background-color:#f55207;
  color:black;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 15px;
  font-weight: bold;
}

/*.servicios__card button:hover {
  background-color: #ec5700;
}*/

.servicios__card:hover {
  transform: scale(1.08);
  box-shadow: 0 0 20px rgba(247, 81, 5, 0.2);
}

/* Estilos para el modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: justify;
  font-weight: bold;
}

.modal-content {
  background-color: #c2c0c0;
  padding: 20px;
  border-radius: 5px;
  max-width: 50%;
  max-height: 100%;
  overflow: auto;
}

.modal-content img {
  width: 100%;
  height: auto;
  max-height: 70vh;
}

/* Estilos para el modal abierto */
.servicios__container.blur {
  filter: blur(80%); /* Aplica desenfoque */
  pointer-events: none; /* Desactiva interacción con los elementos del fondo */
}

.servicios__container.blur .servicios__card {
  filter: blur(0); /* Revierte el desenfoque en las tarjetas */
}

.modal {
  z-index: 1000; /* Asegura que el modal esté por encima del fondo desenfocado */
}


h1{
  color: #f55207;
  text-align: center;
  font-weight: bold;
}






/* Media query para dispositivos móviles */
@media (max-width: 768px) {
  .servicios__container {
    grid-template-columns: repeat(1, 1fr); /* Cambia a una columna */
    gap: 20px; /* Reduce el espacio entre las cards */
  }

  .servicios__card {
    height: auto; /* Altura automática */
  }

  .servicios__card img {
    border-radius: 20px 20px 0 0; /* Ajusta los bordes superiores */
  }
}