.nosotros {
    padding: 50px 0;
    background-color: #f9f9f9;
    opacity: 0;
    position: center;
  }
  
  .nosotros.animate {
    animation: fadeIn 4s ease forwards;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .nosotros__content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .nosotros__text {
    width: 50%;
  }
  
  .nosotros__text h2 {
    color: #f55207;
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .nosotros__text p {
    color: #000000;
    font-size: 1.1rem;
  }
  
  .nosotros__image img {
    width: 90%;
    border-radius: 30px;

  }
  
  @media (max-width: 768px) {
    .nosotros__content {
      flex-direction: column;
      text-align: center;
    }
  
    .nosotros__text {
      width: 100%;
      margin-bottom: 30px;
    }
  
    .nosotros__image img {
      max-width: 80%;
    }
  }
  